@import '../../theme/global';

.tile {
  position: relative;
  width: 2rem;
  height: 2rem;
  font-size: 1.875rem;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    position: absolute;
    content: 'üü´';
  }

  &.tile-active:hover {
    &::before {
      content: 'üü¶';
      cursor: pointer;
    }
  }
  @include disable-text-selection;
}

.tile-visible {
  &.tile-active:hover {
    cursor: default;
  }
}

.tile-flagged {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  &::after {
    position: absolute;
    font-size: 125%;
    top: 50%;
    height: 50%;
    transform: translate(-50%, -50%);
    content: 'üö©';
  }
}

.tile-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  line-height: 1;

  align-items: center;
  justify-content: center;
  display: none;
}

.tile-image-kind-mine {
  &::before {
    content: 'üê∑';
  }
}

.tile-image-kind-empty {
  &::before {
    content: 'üü©';
  }
}

.tile-flagged {
  position: absolute;
  font-size: 75%;
  &::after {
    content: 'üö©';
  }
}

.tile-neighbors {
  position: absolute;
  font-size: 50%;
  font-weight: bold;
  display: none;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -66%);
}

@for $i from 1 through 9 {
  .tile-neighbors-#{$i} {
    &::after {
      content: '#{$i}';
    }
  }
}

.tile-visible {
  .tile-image {
    display: flex;
  }
  .tile-neighbors {
    display: flex;
  }
}
